
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'


import {
jsonpReturn, //处理返回的数据
checkRequest
}
from '@/libs/util'

import {
mapGetters
} from 'vuex'

import {
SendMessage, //发送短信验证码
SetIcon
}
from '@/api/user'

export default {
data() {
return {
logo: require('@/assets/logo.png'),
imgList: [
require('@/assets/center1.png'),
require('@/assets/center2.png'),
require('@/assets/center3.png'),
require('@/assets/center4.png'),
require('@/assets/center5.png'),
require('@/assets/center6.png'),
require('@/assets/center7.png'),
require('@/assets/center8.png')
],
toastText: '', // 弹出框
toast: false,
show: false
}
},
components: {
Group,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
Toast,
Cell,
Confirm
},
computed: {
...mapGetters([
'username',
'telphone',
'token',
])
},
methods: {
openFile() {
this.$refs.file.click();
},
fileChange() {
console.log(this.$refs.file.files[0]);
let file = this.$refs.file.files[0]
let FD = new FormData()
FD.append('token', this.token)
FD.append('image', file)
if (file.size > 30740) {
this.toast = true
this.toastText = '头像图片不能超过3M'
} else {
SetIcon(FD).then((res) => {
this.toast = true
this.toastText = checkRequest(res)
})
}
},
onCancel() {
console.log('on cancel')
},
onConfirm(msg) {
this.$store.dispatch('LogOut').then(() => {
this.$router.push({
name: 'login'
})
})
},
}
}
